import { curry, pathOr } from 'ramda';
import { PredicateVisitor, BREAK, visit } from './visitor'; // first first element in that satisfies the provided predicate
// find :: Pred -> Element -> Element | Undefined

var find = curry(function (predicate, element) {
  var visitor = PredicateVisitor({
    predicate: predicate,
    returnOnTrue: BREAK
  }); // @ts-ignore

  visit(element, visitor);
  return pathOr(undefined, [0], visitor.result);
});
export default find;