import { both, curry, has, pathSatisfies, curryN, pathEq } from 'ramda';
import { isFunction, invokeArgs } from 'ramda-adjunct';
var hasMethod = curry(function (name, obj) {
  return pathSatisfies(isFunction, [name], obj);
});
var hasBasicElementProps = both(has('_storedElement'), has('_content'));
var primitiveEq = curry(function (val, obj) {
  return invokeArgs(['primitive'], [], obj) === val;
});
var hasClass = curry(function (cls, obj) {
  return invokeArgs(['classes', 'includes'], [cls], obj);
});
var isElementType = pathEq(['element']);

var createPredicate = function createPredicate(predicateCreator) {
  return curryN(1, predicateCreator({
    hasMethod: hasMethod,
    hasBasicElementProps: hasBasicElementProps,
    primitiveEq: primitiveEq,
    isElementType: isElementType,
    hasClass: hasClass
  }));
};

export default createPredicate;