import { curry } from 'ramda';
import { ArraySlice } from 'minim';
import { PredicateVisitor, visit } from './visitor'; // finds all elements matching the predicate
// filter :: Pred -> Element -> ArraySlice

var filter = curry(function (predicate, element) {
  var visitor = PredicateVisitor({
    predicate: predicate
  }); // @ts-ignore

  visit(element, visitor);
  return new ArraySlice(visitor.result);
});
export default filter;